import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

//vue-router3.0NavigationDuplicated: Avoided redundant navigation to current location
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch((err) => err);
};

const router = new VueRouter({
    routes: [
        {
            path: '/agent',
            component: () => import('../agent/view/Home'),
            meta: {title: '', keepAlive: false},
            children: [
                {
                    path: 'conversations',
                    component: () => import('../agent/view/Conversations'),
                    meta: {title: '', keepAlive: false},
                    children: [
                        {
                            path: 'chat',
                            component: () => import('../agent/view/Chat'),
                            meta: {title: '', keepAlive: false},
                        }
                    ]
                },
                {
                    path: 'customers',
                    component: () => import('../agent/view/Customers'),
                    meta: {title: '', keepAlive: false},
                }
            ]
        },
        {
            path: '/customer',
            component: () => import('../customer/index')
        }
    ]
});

export default router;
